.NavbarItem {
    background: #212121;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    color: white;
}

.navbar-logo {
    color: white;
    justify-content: start;
    margin-left: 20px;
    cursor: pointer
}

.fa-dog {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 20px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem, 1rem;
}

.nav-links:hover {
    background-color: grey;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItem {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 60px;
        left: -120%;
        opacity: 1;
        transition: all 0.5s ease;
        
    }

    .nav-menu.active {
        background: #2b2b2b;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        justify-content: center;
        padding: 2rem;
        width: 100%;
        display: table;
        transform: translate(-4rem, 0);
    }

    .nav-links:hover {
        background-color: grey;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 20px;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 2rem;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
}